* {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
  text-align: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  width: 90%;
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 10px;
  background: #f9f9f9;
  text-align: center;
}

input, textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

table {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
  overflow-x: auto;
}

th, td {
  padding: 10px;
  border: 1px solid #ddd;
}

thead {
  background-color: #007bff;
  color: white;
}

.bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #222;
  color: white;
  text-align: center;
  padding: 10px;
}

.bottom p span {
  font-weight: 500;
  color: #e2ff09;
}

.btn{
  margin-top: 10px;
  background-color: rgb(169, 14, 14);
  width: 65px;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .container {
    width: 95%;
    margin: 30px auto;
    padding: 15px;
  }

  .App-header {
    font-size: calc(10px + 1.5vmin);
  }

  table {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
  }

  button {
    width: 100%;
  }

  .bottom {
    font-size: 14px;
    padding: 8px;
  }
}

@media (max-width: 480px) {
  .App-header {
    font-size: calc(10px + 1vmin);
  }

  .container {
    padding: 10px;
  }

  th, td {
    padding: 8px;
  }

  .bottom {
    font-size: 12px;
    padding: 6px;
  }
}

@media print{
  input[type="text"]{
    display: none;
  }
  button{
    display: none ;
  }
  tr th:nth-child(5){
    display: none;
  }
  .bottom{
    color: black;
  }
}